@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Lato);
.app{background-color:#7db1f0;padding:0;margin:0;height:100vh;display:flex;align-items:center;justify-content:center;font-family:"Lato"}@media (max-width: 375px){.app{height:auto}}@media (max-width: 375px){.app{border-radius:10px;overflow:hidden}}.app__container{display:flex;border-radius:20px;overflow:hidden;margin-left:0.5rem;margin-right:0.5rem}@media (max-width: 1100px){.app__container{flex-direction:column}}.app__container__left{width:35rem;background-color:#f2fbff;min-height:20rem;position:relative}@media (max-width: 375px){.app__container__left{display:flex;flex-direction:column;justify-content:center;align-items:center;width:375px;margin-top:2rem;margin-bottom:2rem}}.app__container__left .search-icon{width:20px;height:20px;position:absolute;top:2.5rem;left:3.5rem}@media (max-width: 375px){.app__container__left .search-icon{width:0;height:0}}@media (max-width: 375px){.app__container__left form{display:flex;flex-wrap:wrap;justify-content:center;align-items:center}}.app__container__left input{height:3rem;width:14rem;margin-left:3rem;margin-top:1.5rem;margin-right:1rem;border-radius:8px;background-color:white;border-color:white;border:thin;padding-left:3rem;font-size:1rem;color:grey;font-weight:400}@media (max-width: 375px){.app__container__left input{margin-left:1.5rem}}.app__container__left button{border:transparent;outline:none;background-color:#f2fbff;color:#110d3c;font-weight:400;font-size:1rem;cursor:pointer}@media (max-width: 375px){.app__container__left button{font-size:1.2rem}}.app__container__left button:hover{font-weight:bolder}@media (max-width: 375px){.app__container__left button{margin-top:1rem}}.app__container__left h4{margin-top:1.5rem;margin-bottom:1.5rem;color:#110d3c;margin-left:3rem;font-size:2.2rem;font-weight:200}@media (max-width: 375px){.app__container__left h4{margin-left:-1rem;padding-bottom:1rem}}.app__container__left h4 span{color:#110d3c;font-weight:bold}.app__container__right{width:22rem;background-color:#110d3c;display:flex;flex-direction:column;justify-content:center;align-items:center;opacity:0.9}@media (max-width: 1100px){.app__container__right{flex-direction:row;width:35rem}}@media (max-width: 375px){.app__container__right{height:0}}.app__container__error{background-color:#7db1f0;color:white;font-size:1em;border-radius:0 0 3px 3px;margin-left:3rem;width:17rem;text-align:center;text-transform:uppercase;height:0;overflow:hidden;transition:all 0.5s ease-out;box-shadow:2px 2px 2px;line-height:2rem}.show{height:2rem}

.cityCards{display:flex;justify-content:flex-start;max-width:700px;flex-wrap:wrap;margin-left:3rem}@media (max-width: 375px){.cityCards{margin-left:0;justify-content:center;align-items:center;max-width:320px}}

.card{display:flex;justify-content:center;width:110px;flex-direction:column;align-items:center;cursor:pointer;transition:all 0.2s ease;transform:none;position:relative}.card__deleteBtn{font-size:1.4rem;position:absolute;top:-0.9rem;left:5.9rem;color:#110d3c;cursor:pointer;z-index:100}.card__deleteBtn svg{pointer-events:none;border-radius:5px}.card__deleteBtn:hover{color:red;transform:scale(1.1)}@media (max-width: 375px){.card{margin-right:0.3rem}}.card__image{height:106px;width:96px;border-radius:5px}.card__title{color:#110d3c;text-transform:uppercase;pointer-events:none}.card--selected{transform:scale(1.1)}

.days{margin-bottom:3rem;transition:all 0.3s ease}@media (max-width: 375px){.days{width:220px;height:270px;position:relative;margin:40px auto;overflow:hidden;border-radius:20px}}@media (max-width: 375px){.days__container{position:absolute;display:flex;justify-content:space-evenly;left:0;top:0;width:700%;height:100%;font-size:0;transform:translate(0, 0);-webkit-animation:loop 15s linear infinite;animation:loop 15s linear infinite}}.weatherCard{display:flex;margin-right:5rem;margin-left:3rem;justify-content:space-between;align-items:center;color:#110d3c;font-family:"Lato"}@media (max-width: 375px){.weatherCard{flex-direction:column;justify-content:space-evenly;align-items:center;margin:0;width:220px;height:270px;border:1px solid white;background-color:#110d3c;opacity:0.75;color:white;border-radius:10px;padding-bottom:1rem}}.weatherCard h4{width:4rem;margin:0 !important;font-size:0.9rem !important}@media (max-width: 375px){.weatherCard h4{padding-top:1rem;padding-bottom:1rem;font-size:1.2rem !important;font-weight:500;color:white;width:3.5rem}}.weatherCard h5{width:3rem;font-size:0.9rem;padding:0;margin:0}.weatherCard__humidity{width:2rem;height:1rem;display:flex;justify-content:center;align-items:center}@media (max-width: 375px){.weatherCard__humidity{padding-top:1rem;padding-bottom:1rem;width:3rem}}.weatherCard__humidity img{width:0.8rem;height:0.8rem}@media (max-width: 375px){.weatherCard__humidity img{width:1.2rem;height:1.2rem}}.weatherCard__icon{width:2rem;height:2rem}@media (max-width: 375px){.weatherCard__icon{padding-top:0.5rem;padding-bottom:0.5rem;width:3rem;height:3rem}}.weatherCard__minTemp{width:2rem;display:flex}@media (max-width: 375px){.weatherCard__minTemp{padding-top:0.5rem;padding-bottom:0.5rem;width:3rem}}.weatherCard__minTemp img{width:1rem;height:1rem}@media (max-width: 375px){.weatherCard__minTemp img{width:1.2rem;height:1.2rem}}.weatherCard__maxTemp{width:2rem;display:flex}@media (max-width: 375px){.weatherCard__maxTemp{padding-top:0.5rem;padding-bottom:0.5rem;margin-bottom:1rem;width:3rem}}.weatherCard__maxTemp img{width:1rem;height:1rem}@media (max-width: 375px){.weatherCard__maxTemp img{width:1.2rem;height:1.2rem}}.weatherCard p{width:0.9rem;margin:0;padding-left:0.3rem;font-weight:400;font-size:0.8rem}@-webkit-keyframes loop{0%{transform:translate(0, 0)}10%{transform:translate(0, 0)}14%{transform:translate(-14.3%, 0)}24%{transform:translate(-14.3%, 0)}28%{transform:translate(-28.5%, 0)}38%{transform:translate(-28.5%, 0)}44%{transform:translate(-42.9%, 0)}54%{transform:translate(-42.9%, 0)}58%{transform:translate(-57.2%, 0)}68%{transform:translate(-57.2%, 0)}72%{transform:translate(-71.5, 0)}82%{transform:translate(-71.5%, 0)}86%{transform:translate(-85.8, 0)}96%{transform:translate(-85.8%, 0)}100%{transform:translate(0, 0)}}@keyframes loop{0%{transform:translate(0, 0)}10%{transform:translate(0, 0)}14%{transform:translate(-14.3%, 0)}24%{transform:translate(-14.3%, 0)}28%{transform:translate(-28.5%, 0)}38%{transform:translate(-28.5%, 0)}44%{transform:translate(-42.9%, 0)}54%{transform:translate(-42.9%, 0)}58%{transform:translate(-57.2%, 0)}68%{transform:translate(-57.2%, 0)}72%{transform:translate(-71.5, 0)}82%{transform:translate(-71.5%, 0)}86%{transform:translate(-85.8, 0)}96%{transform:translate(-85.8%, 0)}100%{transform:translate(0, 0)}}

.description{color:white;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}@media (max-width: 1100px){.description{flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}}.description__title{display:flex;flex-direction:column;align-items:center;justify-content:center}@media (max-width: 375px){.description__title{width:0px;height:0px}}.description__title img{width:50px;height:50px;padding-right:0.5rem}@media (max-width: 375px){.description__title img{width:0px;height:0px}}.description__title h4{margin:0;font-size:1.8rem;font-weight:lighter;letter-spacing:0.1rem;padding:0}@media (max-width: 375px){.description__title h4{width:0px;height:0px}}.description h3{font-size:5rem;font-weight:normal;margin:0}@media (max-width: 375px){.description h3{width:0px;height:0px}}.description h3 span{font-weight:200;opacity:0.6}.description p{margin:0;font-size:0.9rem;font-weight:200;padding-top:1rem;opacity:0.6;text-transform:capitalize}.chart{color:white;padding-top:1rem}@media (max-width: 1100px){.chart{margin-bottom:2rem}}.chart h4{font-weight:300;font-size:1.2rem}.chart__circle{width:10rem;height:10rem}

